#root,html,body{
  height: 100vh;
}

body{
  background-color: #ffffff;
}
.App{
  width: 100%;
}

*{
  box-sizing: border-box;
  margin: 0;;
  padding: 0;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}

html{
  scroll-behavior: smooth;

}
.navbar{
  background-color: #395B64;
  height: 50px;
  justify-content:center;
  

}
.navlist{
  align-items: center;
  width: 100%;
  justify-content: space-evenly;

}
.navlist li a{
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  
  
}
.navlist li{
  list-style: none;
  margin: 0 10px;
}
.navlist li a:hover{
  color: #ff0000;
  transition: .4s;
}




.flex{
  display: flex;
}


.newsbox{
  display:grid ;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center; 
  max-width: 90%;
  margin: auto;
  overflow: hidden;
  

}
.News_box_heading h2{
  text-transform: uppercase;
  color: #1c2c17;
  cursor: pointer;
}
.newscard{
  
  background-color: white;
  margin: 10px;
  
  
}
.newscard:hover{
 
  color: #eb6452;
  transition: 0.3s;

  
}
.card_info{
  margin: 10px 10px;
}
.card_info h3{
  
  font-size: 20px;
}
.card_info p{
  font-size: 15px;
  color: rgb(107, 104, 99);
  font-family: cursive;
  margin-top: 10px;
 
  overflow: hidden;
}
.card_info a{
  text-decoration: none;
  color: #77b465;
  font-weight: 500;
  
  
}

.card_more a:hover{
  font-weight: 800;
  text-decoration: none;
  color: #88ff00;
 transition: .1s;
  

  
}

.card_more{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  text-align: center;
}



.card_img{
  width: 100%;
  
  background-size: cover;
}

.img_home{
  display: flex;
  height: 300px;
  justify-content: center;
  margin: auto;
}
.News_cmd{
  display: grid;
  max-width: 100%;
  height: 250px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 0 50px;

}

.Cmd_card{
  display: grid;
  background-color: #defa5f;
  color: #0f0e0e;
  margin: 8px;
  text-align: center;
  justify-content: space-around;
  padding: 20px;
  overflow: hidden;
  
}
.Cmd_card h2{
  text-transform: capitalize;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 400;
  
  
}

.Cmd_card h3{
  font-weight: 500;
  padding-bottom: 0;
}
.cmd_para{
  text-transform: capitalize;
  
}
.News_box_heading{
  display: flex;
  width: 100%;
  background-color: #9fb898;
  height: 80px;
  justify-content: center;
  margin:25px 0;
  align-items: center;
}

  

.app_name{
  display: flex;
  width: 25%;
  justify-content: center;
  align-items: center;
  margin-right: 50px;

}
.app_name h3{
  cursor: pointer;
  margin-left: 20px;
  color: rgb(255, 255, 255);
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}
.app_name h3 span{
  color:#ff0000
}

.about_container{
  width: 100%;
  height: 600px;
  background-color: #9fb898;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.about_com{
  background-color: #dfebaa;
  width: 60%;
  height: 450px;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 2fr;
}
.about_com h1{
  margin-top: 20px;
  font-family: 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  text-transform: uppercase;
}
.about_text{
  display: grid;
  width: 90%;
  height: 300px;
  padding: 50px;
  justify-content:space-evenly;
 

}
.about_text p{
  font-family: cursive;
}




/* about us */

.Subscribe{
  background-color: rgb(83, 38, 38);
  color: white;
  padding: 10px 15px 10px;
  display:grid;
  margin: 15px;
  padding-bottom: 20px;
  
}

.section {
  margin-left: 8%;
  margin-right: 8%;  
  text-align: center;
}

.Subscribe p{
  font-size: larger;
  margin-bottom: 5px;
 
}.Subscribe input{
  width: 40%;
  padding: 5px;
  font-size: medium;
  font-weight: 500;
}
.Subscribe button{
  width: 25%;
  height: 40px;
  margin-left: 20px;
  background-color: red;
  color: white;
  border: none;
}
.sub-email{
  display: flex;
  justify-content: center;

}

.section p{
  padding: 10px 0px;
}
i img{
  height: 25px;
}
i p{
  color: black;
  font-size: medium;
  font-style: normal;
  margin-left: 10px;
}

i{
  display: inline-flex;
  align-items: center;

}
.btn:hover{
  background-color: #eb4e27;
}
.contact_list{
  display: flex;
  justify-content: space-evenly;
}
.nav_name{
  display: flex;
  justify-content: center;
  align-items: center;
}





.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}


.about_youtube{
  background-color: #b7c288;
  width: 80%;
  height: 600px;
  display: grid;
  justify-content: center;
   
  
}


span{
  color: #eb4e27;
}
.vid_hed{
  width: 100%;
  margin: 50px;
  
}
.vid_hed p{
  font-weight: 600;
  font-family: cursive;
}

.pro_dec{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  
}
.intro_container{
  
  width: 100%;
  
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
.sc_img{
  width: 100%;
  background-color: #f7f7f7;
}
.sc_img h1{
  margin: 50px 10px;
}

.img_container{
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 50px;
  row-gap: 2px;
  column-gap: 2px;


}
.img_container img{
  width: 100%;
  border: 5px solid black;

}
#scimg{
  margin-top: 50px;
}
iframe{
  border: 2px solid black;

}


@media only screen and (max-width: 700px) {
  body {
    background-color: lightblue;
  }
  .navbar{
    display: grid;
    height: fit-content;
    text-align: center;
    width: 100%;

  }
  .navlist{
    display: grid;
    text-align: center;
  }
  .navlist li{
    margin: 5px 0;
    text-align: center;
    
  }
  .pro_dec{
    font-size: x-small;
  }
  .pro_dec h3{
    text-align: center;
    font-size: small;
  }
  .vid_hed{
    font-size: x-small;
    margin: 0;
    padding: 15px;
  }
  .nav_name{
    width: 100%;
    justify-content: center;

  }
  .app_name{
    width: 100%;
    padding: 25px;
    background-color: #0f0e0e;
  }
  .News_cmd{
    display: flex;
    flex-direction: column;
    height: fit-content;
 
    
  }
  .Cmd_card{
    width: 100%;
    
    margin: 10px;
  }
  .Cmd_card h2{
    font-size: medium;
  }
  .Cmd_card p{
    font-size:x-small;
  }
  .img_home{
    max-width:100%;
  }
  .about_com{
    display: flex;
    flex-direction: column;
    
  }
  .about_text p{
    font-size: smaller;
  }
  .img_container{
    display: flex;
    flex-direction: column;
  }
  .contact_list{
    display: flex;
    flex-direction: column;
    text-align: center;

  }
  .newsbox{
    display: flex;
    flex-direction: column;
  }
  .card_info h3{
    font-size:medium;
  }
  .card_info p{
    font-size: x-small;
  }
  .News_box_heading h2{
    font-size: medium;
  }
}














